body {
  height: 3000px;
}

.about-me {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #f4783d;
  overflow: hidden;
  margin-bottom: 20px;
  margin-left: -112%;

  width: 138%;
}
.about-me h1 {
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  text-align: center;
  font-weight: bold !important;
  text-transform: uppercase;
  color: #ffffff;
  margin-right: -83%;

  width: 175%;
}
@media only screen and (max-width: 988px) {
  .about-me {
    margin-left: -112%;

    width: 170%;
  }

  .about-me h1 {
    margin-right: -83%;

    width: 158%;
  }
}

.about {
  background-color: #e3e6fb;
  font-family: "Exo 2", sans-serif;
  padding-bottom: 20px;
}

.about .card {
  box-shadow: 5px 6px 12px #cc94a8;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;

  border-radius: 0px;
}

.about .card img {
  width: 150px;

  height: 150px;

  margin: auto;

  border-radius: 50%;
}

.about .card h2,
.about .card h4 {
  font-family: "Exo 2", sans-serif;
}

.about .about-info {
  background-color: #fff;

  height: 100%;

  padding: 20px;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;
}

.about .about-info h2 {
  font-family: Arial, Helvetica, sans-serif;

  font-weight: bold;

  font-family: "Exo 2", sans-serif;

  text-transform: uppercase;
}

.about .about-info h2:before {
  content: "";

  border-bottom: 1px solid #cf4b7c;

  border-width: 4px;

  width: 148px;

  position: absolute;

  top: 58px;
}

.education {
  width: auto;
  height: auto;
  /* background-color: #e8aa4d; */
  background-color: #c1cee3;
  padding-bottom: 20px;
  position: relative;
  display: block;
  overflow-x: hidden;
}

.education .education-header {
  width: 138%;
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #f4783d;
  overflow: hidden;
  margin-bottom: 20px;
  margin-left: 18px;
}
.education .education-header h1 {
  -webkit-transform: skew(35deg);

  -ms-transform: skew(35deg);

  -moz-transform: skew(35deg);

  -o-transform: skew(35deg);

  text-align: center;

  color: #ffffff;

  text-transform: uppercase;

  margin-left: 18px;

  float: left;
}

.education .bacholer,
.education .associate,
.education .certs {
  background-color: #fff;

  height: 100%;

  position: relative;

  display: block;
  -o-box-shadow: 5px 6px 12px #cc94a8;
  -ms-box-shadow: 5px 6px 12px #cc94a8;
  box-shadow: 5px 6px 12px #cc94a8;
}

.education img {
  width: 100%;
  height: auto;
  display: block;
}

.main-skills {
  margin-top: 40px;
  background-color: #e3e6fb;
  padding: 22px 0px;
  margin-top: 0px;
}
.main-skills .skill-header {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #f4783d;
  overflow: hidden;
  margin-bottom: 20px;
  margin-left: -124%;
  width: 138%;
  margin-top: -22px;
}
.main-skills .skill-header h1 {
  margin-left: 90%;
  width: 138%;
  transform: skew(35deg);
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  color: #fff;
}

@media only screen and (max-width: 988px) {
  .main-skills .skill-header {
    margin-left: -87%;
    width: 130%;
  }
  .main-skills .skill-header h1 {
    margin-left: 66%;
    width: 138%;
  }
}

.main-skills h1 {
  color: #666;
}
.main-skills .progress {
  background-color: #ffffff;
  border-radius: 50px;
  box-shadow: 3px 5px 7px #ccc;
  -webkit-box-shadow: 3px 5px 7px #ccc;
  -moz-box-shadow: 3px 5px 7px #ccc;
  -ms-box-shadow: 3px 5px 7px #ccc;
  -o-box-shadow: 3px 5px 7px #ccc;
}
.main-skills .progress-bar {
  border-radius: 50px;
  font-weight: bold;
  color: #0e1d25;
}


.contact_form {
