  background-image: url(/mahmoudaosman/static/media/contact-us.ad26b71e.png);
  display: block;
  overflow: hidden;
  position: relative;
  margin-bottom: 40px;
}
.contact_form .form-item {
  background-color: #fff;
}

.footer {
  background-color: #272727;
  color: #a9a1a1;
}
.footer .footer-copyright {
